<template>
  <div style="margin:24px;">
    <div class="item-wrap">
      <label class="title_wrap">公司名称</label>
      <div class="content-wrap">
        <el-input v-model="detailInfo.title" v-if="status" class="business_name_edit"></el-input>
        <span class="business_name" v-if="detailInfo.title&&!status">{{detailInfo.title}}</span>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap">公司负责人姓名</label>
      <div class="content-wrap">
        <el-input v-model="detailInfo.admin_name" v-if="status" class="business_name_edit"></el-input>
        <span class="business_name" v-if="detailInfo.admin_name&&!status">{{detailInfo.admin_name}}</span>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap">公司负责人电话</label>
      <div class="content-wrap">
        <el-input v-model="detailInfo.admin_phone" v-if="status" class="business_name_edit"></el-input>
        <span class="business_name" v-if="detailInfo.admin_phone&&!status">{{detailInfo.admin_phone}}</span>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap">公司所在城市</label>
      <div class="content-wrap">
        <div v-if="status" class="business_name_edit">
          <el-select style="margin-bottom: 10px;" v-model="detailInfo.province_code" placeholder="请选择省" @change="changeProvince">
            <el-option v-for="item in provinceData" :key="item.code" :label="item.name" :value="item.code" ></el-option>
          </el-select>
          <el-select style="margin-bottom: 10px;" v-model="detailInfo.city_code" placeholder="请选择市" @change="changeCity">
            <el-option v-for="item in cityData" :key="item.code" :label="item.name" :value="item.code" ></el-option>
          </el-select>
          <el-select v-model="detailInfo.cd_code" placeholder="请选择区">
            <el-option v-for="item in areaData" :key="item.code" :label="item.name" :value="item.code" ></el-option>
          </el-select>
        </div>
        <span class="business_name" v-if="detailInfo.business_city&&!status">{{detailInfo.business_city}}</span>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap">公司具体地址</label>
      <div class="content-wrap">
        <el-input v-model="detailInfo.addr_info" v-if="status" class="business_name_edit"></el-input>
        <span class="business_name" v-if="detailInfo.addr_info&&!status">{{detailInfo.addr_info}}</span>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap" style="vertical-align:top;">公司营业执照</label>
      <div class="content-wrap">
        <upload-image use="logo" image-size="50" @uploadSucc="uploadLicenseSuccess">
          <img v-if="detailInfo.business_license_img" :src="detailInfo.business_license_img" class="avatar needsclick">
          <div v-else >
            <i class="el-icon-plus avatar-uploader-icon needsclick"></i>
          </div>
        </upload-image>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap">公司logo</label>
      <div class="content-wrap">
        <upload-image use="logo" image-size="50" @uploadSucc="uploadSuccess">
          <img v-if="detailInfo.logo" :src="detailInfo.logo" class="avatar needsclick">
          <div v-else>
            <i class="el-icon-plus avatar-uploader-icon needsclick"></i>
          </div>
        </upload-image>
      </div>
    </div>
    <div class="item-wrap">
      <label class="title_wrap">公司介绍</label>
      <div class="content-wrap">
        <el-input v-model="detailInfo.remark" v-if="status" type="textarea" class="remark_edit"></el-input>
        <span class="business_name" v-if="detailInfo.remark&&!status">{{detailInfo.remark}}</span>
      </div>
    </div>
    <div style="margin-top:24px; display:block; float:left; width:100%; text-align:center;  margin-bottom:36px;">
      <el-button type="primary" v-if="!status" v-on:click="editClick" >编辑</el-button>
      <div v-else>
        <el-button type="primary" v-on:click="updateEssentialInfoData">确认</el-button>
        <el-button type="primary" v-on:click="cancelClick">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import uploadImage from '../../../components/UploadImage.vue'
import { getLoginInfo } from '../../../assets/common/utils.js'
import {obtailEssentialInfo, obtainProvinceAddress, updateEssentialInfo} from '../../../service/SystemService.js'
export default {
  data () {
    return {
      detailInfo: {
        title: '管家帮',
        admin_name: 'aa',
        remark: 'aa',
        logo: '',
        business_license_img: '',
        addr_info: 'aa',
        business_city: 'aa',
        admin_phone: 'aa',
        cd_info: {},
        city_info: {},
        province_info: {},
        cd_code: '',
        city_code: '',
        province_code: ''
      },
      id: 0,
      status: false,
      provinceData: [],
      cityData: [],
      areaData: []
    }
  },
  components: {
    'upload-image': uploadImage
  },
  created () {
    // 面包屑
    this.$store.dispatch('commitChangeTopTitles', ['系统设置', '基本信息'])
    this.id = getLoginInfo('company_id')
    this.obtainProvinceAddressData()
    this.obtailEssentialInfoData()
  },
  methods: {
    uploadSuccess (imagePath) {
      this.detailInfo.logo = imagePath.path
    },
    uploadLicenseSuccess (imagePath) {
      this.detailInfo.business_license_img = imagePath.path
    },
    editClick () {
      this.status = true
    },
    cancelClick () {
      this.status = false
    },
    obtailEssentialInfoData () {
      let _this = this
      obtailEssentialInfo(this.id).then(res => {
        _this.detailInfo = res.data
        if (_this.detailInfo.province_info) {
          if (_this.detailInfo.city_info) {
            if (_this.detailInfo.cd_code) {
              _this.detailInfo.business_city = _this.detailInfo.province_info.name + _this.detailInfo.city_info.name + _this.detailInfo.cd_info.name
            } else {
              _this.detailInfo.business_city = _this.detailInfo.province_info.name + _this.detailInfo.city_info.name
            }
          } else {
            _this.detailInfo.business_city = _this.detailInfo.province_info.name
          }
        }
        if (_this.detailInfo.province_code) {
          _this.provinceData.forEach(function (item) {
            if (item.code === _this.detailInfo.province_code) {
              _this.obtainCityAddressDataSecond(item.id, _this.detailInfo.city_code)
            }
          })
        }
      })
    },
    obtainProvinceAddressData () {
      let _this = this
      obtainProvinceAddress('?pid=-1').then(res => {
        _this.provinceData = res.data
      })
    },
    obtainCityAddressData (pid) {
      obtainProvinceAddress('?pid=' + pid).then(res => {
        this.cityData = res.data
      })
    },
    obtainCityAddressDataSecond (pid, cdCode) {
      let _this = this
      obtainProvinceAddress('?pid=' + pid).then(res => {
        _this.cityData = res.data
        console.log(_this.cityData)
        if (cdCode) {
          _this.cityData.forEach(function (item) {
            if (item.code === cdCode) {
              _this.obtainAreaAddressData(item.id)
            }
          })
        }
      })
    },
    obtainAreaAddressData (pid) {
      obtainProvinceAddress('?pid=' + pid).then(res => {
        this.areaData = res.data
      })
    },
    changeProvince (data) {
      let _this = this
      this.provinceData.forEach(function (item) {
        if (item.code === data) {
          _this.obtainCityAddressData(item.id)
          _this.detailInfo.city_code = ''
          _this.detailInfo.cd_code = ''
        }
      })
    },
    changeCity (data) {
      let _this = this
      this.cityData.forEach(function (item) {
        if (item.code === data) {
          _this.obtainAreaAddressData(item.id)
          _this.detailInfo.cd_code = ''
        }
      })
    },
    updateEssentialInfoData () {
      if (!this.detailInfo.admin_name) {
        this.$message({
          type: 'error',
          message: `请输入公司负责人姓名`
        })
        return
      }
      if (!this.detailInfo.admin_phone) {
        this.$message({
          type: 'error',
          message: `请输入公司负责人手机号`
        })
        return
      }
      if (!this.detailInfo.province_code) {
        this.$message({
          type: 'error',
          message: `请选择公司所在省份`
        })
        return
      }
      if (!this.detailInfo.city_code) {
        this.$message({
          type: 'error',
          message: `请选择公司所在城市`
        })
        return
      }
      if (!this.detailInfo.cd_code) {
        this.$message({
          type: 'error',
          message: `请选择公司所在区`
        })
        return
      }
      if (!this.detailInfo.addr_info) {
        this.$message({
          type: 'error',
          message: `请输入公司具体地址`
        })
        return
      }
      if (!this.detailInfo.business_license_img) {
        this.$message({
          type: 'error',
          message: `请选择公司营业照`
        })
        return
      }
      if (!this.detailInfo.logo) {
        this.$message({
          type: 'error',
          message: `请选择公司logo`
        })
        return
      }
      if (!this.detailInfo.remark) {
        this.$message({
          type: 'error',
          message: `请输入公司介绍`
        })
        return
      }
      let params = {
        id: this.id,
        title: this.detailInfo.title,
        admin_phone: this.detailInfo.admin_phone,
        admin_name: this.detailInfo.admin_name,
        province_code: this.detailInfo.province_code,
        city_code: this.detailInfo.city_code,
        cd_code: this.detailInfo.cd_code,
        addr_info: this.detailInfo.addr_info,
        remark: this.detailInfo.remark,
        business_license_img: this.detailInfo.business_license_img,
        logo: this.detailInfo.logo
      }
      console.log(params)
      updateEssentialInfo(params).then(res => {
        this.status = false
        this.$message({
          type: 'success',
          message: `修改成功`
        })
        this.obtainProvinceAddressData()
        this.obtailEssentialInfoData()
      })
    }
  }
}
</script>

<style scoped>
div >>>.avatar-uploader-icon {
  width: 200px;
  float: right;
}
.item-wrap {
  display: flex;
  flex-direction: column;
}
.title_wrap{
  color:white;
  background:#75ace6;
  display: block;
  /* float: left; */
  margin-top:20px;
  width: 50%;
  /* height: 50px; */
  font-size: 13px;
  padding: 8px;
  box-sizing: border-box;
  border-radius: 5px 1px 5px 5px;
  text-align: center;
  margin-bottom: 10px;
}
.content-wrap {
  display: flex;
}
.business_name_edit{
  /* margin-left: 30px; */
  display: flex;
  flex-direction: column;
}
.business_name{
  background: #e3eefa;
  width: 100%;
  /* float: right; */
  box-sizing: border-box;
  /* height: 30px; */
  /* margin-top: 30px; */
  text-align: left;
  padding: 10px 20px;
  /* margin-left: 20px; */
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 278px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px solid;
  font-weight: bold;
}
.avatar {
  width: 278px;
  height: 178px;
  display: block;
  object-fit: cover;
}
.remark_edit{
  width: 300px;
  /* margin-left: 30px; */
}
</style>



